.feelings a{float:left;line-height:40px;border:1px solid #ccc;padding:0px 10px;color:#5e5e5e;margin-right:10px;
}
.feelings a:hover{border:1px solid #e3101e;color:#e3101e
}
.feelings .select{border:1px solid #e3101e;background:url(data:image/png;base64,) right bottom no-repeat;
}
.judge_area{
	float:left;width:735px;height:70px;margin-top:10px;border:1px solid #ccc;outline:none;padding:15px;
}
.judge_sub{
	width:100px;height:32px;background-color:#fe0000;border:0px;font-size:14px;color:#fff;font-family:'Microsoft Yahei';outline:none;cursor:pointer;text-align:center;margin-top:10px;margin-left:120px;
}
.no_name{margin-left:15px;
}
.judge_list_con{
	margin-top:10px;
	overflow: hidden;
	width:100%;
}
.judge_list_con li{
	overflow:hidden;
	border-bottom:1px solid #ededed;
	padding:20px 0;
}
.user_info{
	width:200px;
}
.user_info img{
	width:40px;
	height:40px;
	border-radius:20px;
	float:left;
}
.user_info b{
	line-height:40px;
	float: left;
	font-weight:normal;
	margin-left:20px;
	font-size:12px;
}
.judge_info{
	width:780px;
}
.stars_1,.stars_2,.stars_3,.stars_4,.stars_5{
	width:85px;
	height:17px;
	background:url(data:image/png;base64,) left -80px no-repeat;
	margin-bottom:10px;
}
.stars_1{
	background-position:left -16px;
}
.stars_2{
	background-position:left -32px;
}
.stars_3{
	background-position:left -48px;
}
.stars_4{
	background-position:left -64px;
}
.judge_detail{
	font-size:12px;
	line-height:20px;
}
.find_header{
	width: 990px;
	height: 120px;
	margin:0px auto;
}
.find_header img{
	float:left;
	margin-top:30px;
}
.find_form{
	width: 990px;
	height: 450px;
	border:1px solid #e6e6e6;
	margin:0px auto 30px;
}
.step{
	width:988px;
	height:50px;
	margin:0px auto;
	background:url(data:image/png;base64,) no-repeat;
	margin-top:75px;
}
.step-1{
	background-position:0px -150px;
}
.step-2{
	background-position:0px -100px;
}
.step-3{
	background-position:0px -50px;
}
.step-4{
	background-position:0px 0px;
}
.form_step{
	width:430px;
	height:200px;
	margin:70px auto 0;
}
.form_step .form_group{
	height:45px;
	margin-bottom:10px;
	position:relative;
}
.form_step .form_group label{
	width:100px;
	float:left;
	text-align:right;
	font-size:14px;
	height:40px;
	line-height:40px;
}
.form_step .form_group .input_txt{
	width:300px;
	height:25px;
	border:1px solid #ddd;
	float:left;
	outline:none;
	margin-top:7px;
	text-indent:10px;
}
.form_step .form_group .input_txt2{
	width:180px;
}
.form_step .form_group .pic_code{
	width:110px;
	height:27px;
	margin-left:10px;
	margin-top:7px;
}
.form_step .form_group .input_sub{
	width:100px;
	height:26px;
	background:#c00;
	color:#fff;
	border:0px;
	margin:10px 0px 0px 100px;
	cursor:pointer;
}
.form_step .form_group .error{
	position:absolute;
	left:100px;
	top:40px;
	color:red;
	font-size:12px;
}
.form_step .form_group .phone_code{
	float:left;
	width:110px;
	line-height:25px;
	text-align:center;
	border:1px solid #ddd;
	margin-top:7px;
	margin-left:8px;
	background:#fff;
	color:#333;
}
.pass_change_finish{
	text-align:center;
	margin-top:100px;
	color:red;
	font-size:20px;
}
.order_success{
	margin:30px auto;
	background:url(data:image/png;base64,) 50px center no-repeat;
}
.order_success p{
	text-indent:140px;
	margin-bottom:10px;
}
.order_success p em{
	font-size:20px;
	color:#fe0000;
}
.order_success p a{
	color:#f80;
}
.order_success p a:hover{
	text-decoration:underline;
}
.time_count_bar{
	width:730px;
	height:39px;
	background:url(data:image/png;base64,);
	margin-bottom:10px;
}
.count_icon{
	float:left;
	height:39px;
	background:url(data:image/png;base64,) 10px center no-repeat;
	line-height:39px;
	color:#fff;
	text-indent:30px;
	font-size:14px;
}
.time_count_bar .time_count span{
	color:#fff;
}
.time_count_bar .time_count b{
	background:#333;
}
.time_count_bar .time_count i{
	color:#fff;
}
/*  确认弹框 */
.pop_con2{
	display:none;
}
.confirm_pop{
	width:350px;
	height:160px;
	background:#fff;
	border:1px solid #dddddd;
	background-color:#f7f7f7;
	position:fixed;
	left:50%;
	margin-left:-176px;
	top:50%;
	margin-top:-81px;
	z-index:1000;
}
.confirm_pop p{
	margin:30px 0 0 40px;
	font-size:16px;
	font-family:'Microsoft Yahei'
}
.confirm_pop_title{
	background:#810101;
	height:30px;
}
.confirm_pop_title h3{
	color:#fff;
	float: left;
	line-height:30px;
	text-indent:20px;
	font-size:16px;
}
.confirm_pop_title a{
	color:#fff;
	float:right;
	font-size:26px;
	margin-right:10px;
	line-height:30px;
}
.confirm_submit{
	width:80px;
	height:30px;
	background-color:#fe0000;
	border:0px;
	color:#fff;
	margin:30px 0px 10px 80px;
	cursor:pointer;
	font-family:'Microsoft Yahei'
}
.confirm_cancel {
	background-color:#bd0c0c;
	margin-left:30px;
}
/*收货地址错误提示*/
.receiver_error,.mobile_error,.place_error,.tel_error,.email_error {
	color:#f00;margin-left:5px;
}
/*修改密码错误提示*/
.old_pwd_error,.new_pwd_error,.new_cpwd_error {
	color:#f00;margin-left:5px;
}
/*修改分页插件样式*/
.ui-pagination-container {
	line-height: 20px;
}
.ui-pagination-container .ui-pagination-page-item.active {
	background: #f80000;
	border-color: #f80000;
}
/*详情页添加数量提示样式*/
.overtip{
	width:92px;
	height:25px;
	border:1px solid #ddd;
	position:absolute;
	left:725px;
	top:396px;
	z-index:999;
	text-align:center;
	line-height:25px;
	font-size:12px;
	color:#666;
	background:#fff;
	border-left:1px solid #e62834;
	display:none;
}
.overtip i{
	position:absolute;
	left:-5px;
	top:10px;
	width:5px;
	height:7px;
	background: url(data:image/png;base64,);
}
/* 把标签默认的间距设为0 */
body,ul,ol,p,h1,h2,h3,h4,h5,h6,dl,dd,select,input,textarea,form{margin:0;padding:0;font-size:12px;
}
/* 让h标签文字大小继承body的文字设置 */
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal;
}
/* 去掉列表默认的图标 */
ul,ol{list-style:none;
}
/* 去掉em默认的斜体 */
em{font-style: normal;
}
/* 去掉a标签默认的下划线 */
a{text-decoration:none;
}
/* 去掉加链接时产生的框线 */
img{border:0;
}
/* 清除浮动 */
.clearfix:before,.clearfix:after{content:"";display:table
}
.clearfix:after{clear:both;
}
.clearfix{zoom:1
}
/* 浮动 */
.fl{float:left
}
.fr{float:right
}






















































































































































































































































